import { Component, OnInit } from '@angular/core';
import {Platform} from '@ionic/angular';
import { AppVersion } from '@ionic-native/app-version/ngx';

import {StoreService} from '../services/store.service';


@Component({
  selector: 'app-appinfo',
  templateUrl: './appinfo.page.html',
  styleUrls: ['./appinfo.page.scss'],
})
export class AppinfoPage implements OnInit {
  backend: string;
  backButtonText = '';

  constructor(private plt: Platform, private appVersion: AppVersion, private store: StoreService) { }

  async ngOnInit() {
    this.backend = await this.store.getEnv();
  }

  updateBackend() {
    this.store.switchUserEnv(null, this.backend);
  }
}
